import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import calc from '@/pages/calc'
import index from '@/pages/index'
import msgLottery from '@/pages/msgLottery'
import stWelfare from '@/pages/stWelfare'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      // name: 'index',
      // // component: () => import('@/pages')
      // component: index
      redirect: '/index.html'
    },
    {
      path: '/index.html',
      name: 'index',
      // component: () => import('@/pages')
      component: index
    },
    {
      path: '/calc/index.html',
      name: 'calc',
      // component: () => import('@/pages/calc')
      component: calc
    },
    {
      path: '/msgLottery/index.html',
      name: 'msgLottery',
      // component: () => import('@/pages/msgLottery')
      component: msgLottery
    },
    {
      path: '/stWelfare/index.html',
      name: 'stWelfare',
      // component: () => import('@/pages/stWelfare')
      component: stWelfare
    }
  ]
})



